.container {
    padding: 8px 0;
    background-color: #E2E9FF;
}

.title {
    padding: 24px;
    background: linear-gradient(97deg, #556BFF 12%, #B24EFF 103%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-weight: bold;
}

.panel {
    border-radius: 12px;
    background: #F4F7FE;
    border: 1px solid #FFFFFF;
    padding: 24px;
}

.icon {
    display: block;
}

.panel-title {
    margin-left: 8px;
    color: #020C1A;
    font-size: 18px;
    font-weight: 600;
}

.inner-title {
    color: #020C1A;
    font-size: 12px;
    font-weight: 600;
    margin-top: 16px;
}

.panel-tip {
    font-size: 12px;
    color: #70749E;
    margin-left: 12px;
}

.panel-tag {
    border-radius: 6px;
    background: linear-gradient(274deg, rgba(139, 183, 243, 0.1) 1%, rgba(153, 122, 214, .1) 93%);
    padding: 2px 14px;
    color: #020C1A;
    font-weight: 600;
    margin-left: 16px;
}

.inner {
    background: #FFFFFF;
    border-radius: 8px;
    flex: 1;
    padding: 0 16px;
    overflow: hidden;
}

.inner-1 {
    margin-top: 16px;
    background: #FFFFFF;
    border-radius: 8px;
    flex: 1;
    padding: 16px;
}

.mirror {
    display: block;
}

.mirror-text {
    padding: 7px 20px;
}

th.ant-table-cell {
    color: #353966 !important;
}

.ant-table-cell {
    border-bottom: none !important;
}

.ant-table-cell::before {
    display: none !important;
}

.flex-1 {
    flex: 1;
}

.text {
    font-size: 12px;
    color: #020C1A;
}

.arrow-icon {
    transform: scale(0.8);
    color: #020C1A;
}

.tip {
    color: #70749E;
    font-size: 12px;
    margin-right: 8px;
}

.ft-weight-500 {
    font-weight: 500;
}

.dashboard {
    position: relative;
    height: 626px;
}

.dashboard .bg {
    position: absolute;
    top: 24px;
    bottom: 12px;
    left: 50%;
    margin-left: -493px;
}

.dashboard span {
    position: absolute;
    font-size: 30px;
    cursor: pointer;
    width: 122px;
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #020C1A;
}

.dashboard .name {
    position: absolute;
    font-size: 30px;
    cursor: pointer;
    width: 180px;
    padding: 0 10px;
    color: #020C1A;
    top: 269px;
    left: 397px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.dashboard span[aria-disabled=false]:hover {
    background: linear-gradient(107deg, #556BFF 16%, #B24EFF 101%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.dashboard span[aria-disabled=true] {
    cursor: not-allowed;
    opacity: 0.2;
}

.dashboard .absolute {
    position: absolute;
}